.sweetContainer{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: column;
  min-width: 512px;
  max-height: 512px;
  /* 
  min-width: 512px;
   */
  overflow: auto;

}



.sweetContent{
  width: 100%;
  overflow: auto;
}


.twitterIcon{
  font-size: 50px;
  margin-bottom: 20px;
  color:#00acee;
}
/* 0 ~ 512px */
@media screen and (max-width:512px){
  .sweetContainer{
    min-width: 100vw;
    overflow: hidden;
  }
}
/* 768 ~ px */
@media screen and (min-width:768px){
  .sweetContainer{
    min-width: 768px;
    min-height: 768px;

  }
}